.form-section {
    padding-top: 5.6rem;

    form {
        ul {
            margin: 0 auto;
            max-width: 56rem;

        }

        .bottom-desc {
            width: fit-content;
            margin: 4.0rem auto 0 auto;
            text-align: left;
            line-height: 2.8rem;
        }
    }
}


.contactus-section {
    .container {
        max-width: 108rem;
    }

    .top-desc {
        margin-bottom: 4.4rem;
    }

    .form-wrapper {
        display: flex;
        flex-direction: column;
        row-gap: 2.4rem;
        margin-bottom: 6.4rem;
    }

    .form-row {
        position: relative;
        width: 100%;
        display: flex;
        column-gap: 1.6rem;

        &.with-inputbox {
            .label {
                margin-top: 1.0rem;
            }
        }

        &.privacy {
            align-items: center;
            justify-content: center;
            column-gap: 0.6rem;

            #privacy-policy {
                margin-top: 0.1rem;
            }
        }
    }

    .btn {
        margin: 0 auto;
        font-family: inherit;
        font-size: inherit;
    }

    .btn-blue {
        background-color: $dodgerblue;
        border: unset;

        &:before {
            display: none;
        }

        &:after {
            display: none;
        }

        &:hover {
            color: $white_color;
            opacity: 0.8;
        }
    }

    .label {
        width: 26rem;
        height: fit-content;
        flex-shrink: 0;

        span {
            display: inline;
        }
    }

    .input-wrapper {
        width: 100%;

        &.horizontal-wrapper {
            justify-content: flex-start;
            column-gap: 4.4rem;
        }
    }

    .input-item {
        display: flex;
        column-gap: 0.4rem;
        align-items: center;
    }

    .input-label {
        width: 7.1rem;
        flex-shrink: 0;
    }

    .width1 {
        max-width: 14rem;
    }

    .width2 {
        max-width: 64rem;
    }

    .width3 {
        max-width: 58rem;
    }

    .vertical-wrapper {
        width: 100%;
        display: flex;
        flex-direction: column;
        row-gap: 1.6rem;
    }

    .horizontal-wrapper {
        width: 100%;
        display: flex;
        flex-direction: row;
        align-items: center;
        column-gap: 1.6rem;
    }

    #province {
        max-width: 36.2rem;
    }

    .input-privacy {
        width: fit-content;
        margin: 1rem auto 0 auto;

        input {
            margin-right: 0.8rem !important;
        }

        a {
            text-decoration: underline;

            &:hover {
                opacity: 0.7;
            }
        }

        label {
            display: flex;
            align-items: center;
        }
    }

    .checkbox-item {
        -webkit-row-gap: 2.4rem;
        -moz-row-gap: 2.4rem;
        row-gap: 2.4rem;
    }

    textarea {
        line-height: 2.4rem;
    }

    .btn {
        padding: 2.3rem 6.4rem;
        height: 6.4rem;
        width: fit-content;
    }

    .btn-black {
        &::before {
            background: #2175D6;
        }

        &:hover span {
            color: #FFF;
        }
    }

    .btns-wrapper {
        margin: 4.8rem auto 0 auto;
    }

    &.main-confirm {

        input,
        textarea {
            padding: 0;
            border: unset;
            font-weight: 400;
        }

        .name-wrapper {
            .horizontal-wrapper {
                justify-content: flex-start;

                input {
                    width: 3.4rem;
                }
            }
        }
    }

    label {
        &:hover {
            cursor: pointer;
        }
    }

    .required {
        width: fit-content;
        position: relative;
        display: flex;
        align-items: center;
        column-gap: 0.8rem;

        &::after {
            width: fit-content;
            content: '';
            color: #F00;
            line-height: 100%;
        }
    }
}

input[type=radio] {
    width: 1.8rem;
    height: 1.8rem;
}

input[type=checkbox] {
    position: relative;
    border-radius: 4px;
    border: 1.5px solid #333;
    background: none;
    cursor: pointer;
    line-height: 0;
    outline: 0;
    padding: 0 !important;
    vertical-align: text-top;
    height: 2rem !important;
    width: 2rem !important;
    -webkit-appearance: none;
    opacity: .5;
    margin: 0.3rem 0.8rem 0 0;
}

input[type=checkbox]:hover {
    opacity: 1;
}

input[type=checkbox]:checked {
    background-color: $dodgerblue;
    border: 1.5px solid #FFF;
    opacity: 1;
}

input[type=checkbox]:checked:before {
    content: '';
    position: absolute;
    right: calc(50% + 0rem);
    top: 50%;
    width: 0.6rem;
    height: 1.3rem;
    border: solid #FFF;
    border-width: 0 2px 2px 0;
    margin: -1px -1px 0 -1px;
    transform: rotate(45deg) translate(-50%, -50%);
    z-index: 2;
}